@import url("https://fonts.googleapis.com/css2?family=Roboto&family=Ubuntu:wght@500&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  line-height: 1.2 !important;
}

.navbar {
  background-color: #060b26;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 0.5rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: #060b26;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -180px;
  transition: 0.3s;
}

.nav-menu.active {
  left: 0;
  transition: 0.3s;
}

.nav-text {
  padding: 8px 0px 8px 16px;
  list-style: none;
}

.nav-text a {
  text-decoration: none;
  color: white !important;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 16px;
  border-radius: 4px;
  cursor: pointer;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  cursor: pointer;
  padding: 20px;
}

span {
  margin-right: 16px;
  display: flex;
  align-items: flex-end;
}

li.active a span svg {
  transform: rotate(-180deg);
  transition: 0.4s;
}

li a span svg {
  transform: rotate(0deg);
  transition: 0.4s;
}

li.have-children ul {
  padding: 0px;
}
li.have-children,
li {
  position: relative;
}
.have-children > ul {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  -moz-transition: opacity 0.3s, max-height 0.3s;
  -ms-transition: opacity 0.3s, max-height 0.3s;
  transition: opacity 0.3s, max-height 0.3s;
  _display: none;
}
li.have-children.active > ul {
  max-height: 250px;
  overflow-y: auto;
  opacity: 1;
  _display: block;
}

li.have-children li a {
  font-size: 16px;
}

.table {
  margin-top: 150px;
}

th {
  cursor: pointer;
}

thead th:hover {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.075);
}
